@use '../../../packages/utils/aristobyte-ui.settings.scss' as *;

:root {
  color-scheme: light;
  font-family:
    'Space Grotesk',
    ui-sans-serif,
    system-ui,
    -apple-system,
    Segoe UI,
    sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  background:
    radial-gradient(1200px 600px at 10% -10%, rgba(56, 189, 248, 0.18), transparent 60%),
    radial-gradient(900px 500px at 90% 0%, rgba(148, 163, 184, 0.14), transparent 55%),
    #0b0c10;
  color: #f0f4f8;
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100vh;
}

.header {
  display: flex;
  gap: 8px;
  width: 100vw;

  &__container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 1200px;
    padding: 10px 30px;
    width: 100%;
  }

  &__logo {
    align-items: center;
    border-radius: 999px;
    color: #f6f7f9;
    display: flex;
    font-size: 32px;
    font-family: Righteous;
  }

  &__title {
    font-size: 26px;
    letter-spacing: -0.02em;
    margin: 0;
  }

  &__subtitle {
    color: rgba(240, 244, 248, 0.65);
    font-size: 14px;
    line-height: 1.6;
    margin: 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    text-align: right;
  }
}

.main {
  display: flex;
  flex-direction: column;
  flex: 1;

  &__container {
    display: flex;
    flex: 1;
    margin: 0 auto;
    max-width: 1200px;
    padding: 20px 30px;
    width: 100%;
  }

  &__sandbox {
    display: grid;
    gap: 28px;
    margin: 0 auto;
    flex: 1;
    width: 100%;
  }

  &__sandbox-panel {
    align-items: center;
    background: rgba(17, 20, 27, 0.78);
    border: 1px solid rgba(148, 163, 184, 0.25);
    border-radius: 18px;
    display: grid;
    gap: 16px;
    justify-items: center;
    padding: 24px;
    height: 100%;
    min-height: 0;
  }
}

.footer {
  &__container {
    align-items: center;
    color: rgba(240, 244, 248, 0.55);
    display: flex;
    font-size: 12px;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 1200;
    padding: 10px 30px;
    width: 100%;

    p {
      text-align: center;
      width: 100%;
    }
  }
}
